body {
  height: 100vh;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  -ms-align-items: center;
  align-items: center;
  background: linear-gradient(#e66465, #9198e5);
}
.parent {
  width: 900px;
  height: 500px;
  border: 1px solid #000;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  font-size: 25px;
  position: relative;
}
.side {
  width: 50%;
  text-align: center;
  background: rgba(16, 18, 27, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.18);
  backdrop-filter: blur(1px);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.side > .player-0-score,
.side > .player-1-score {
  transform: translate(0, -120px);
}
.player-0-name > span,
.player-1-name > span {
  position: relative;
}

.active {
  background: rgba(255, 255, 255, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.18);
  backdrop-filter: blur(0.5px);
}
.active > .player-0-name > span::after,
.active > .player-1-name > span::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: red;
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(10px, -50%);
  border-radius: 50%;
}

button {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  cursor: pointer;
  font-size: 25px;
  color: green;
  outline: 0;
}
.new {
  top: 100px;
}
.role {
  top: 300px;
}
.hold {
  top: 380px;
}
.img-dice {
  width: 100px;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 200px;
  border: 1px solid #000;
}

#winner {
  background-color: #0a77b0;
}
